import React from 'react';
import { Typography, Grid } from '@material-ui/core';
import CardComponent from './Card/Card';
import styles from './Cards.module.css';

const Info = ({ data: { confirmed, recovered, deaths, lastUpdate } }) => {
  if (!confirmed) {
    return 'Loading...';
  }

  return (
    <div className={styles.container}>
        <Typography gutterBottom variant="h4" component="h2">รายงานสถานการณ์ COVID-19</Typography>
      <Grid container spacing={3} justify="center">
        <CardComponent
          className={styles.infected}
          cardTitle="ผู้ติดเชื้อสะสม"
          value={confirmed.value}
          lastUpdate= {lastUpdate}
          cardSubtitle="จำนวนผู้ติดเชื้อจาก COVID-19."
        />
        <CardComponent
          className={styles.recovered}
          cardTitle="ผู้ที่รักษาหายแล้ว"
          value={recovered.value}
          lastUpdate={lastUpdate}
          cardSubtitle="จำนวนผู้ที่รักษาหายแล้วจาก COVID-19"
        />
        <CardComponent
          className={styles.deaths}
          cardTitle="ผู้เสียชีวิต"
          value={deaths.value}
          lastUpdate={lastUpdate}
          cardSubtitle="จำนวนผู้เสียชีวิตจาก COVID-19."
        />
      </Grid>
    </div>
  );
};

export default Info;